<template>
  <v-container fluid>
    <v-layout row wrap>

      <v-flex xs6>
        <v-subheader>Type de congs</v-subheader>
      </v-flex>
      <v-flex xs6>
        <v-select
          :items="items"
          v-model="e1"
          label="Type de congs"
          single-line
        ></v-select>
